@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@400;700&family=Montserrat:ital,wght@1,900&display=swap);
.site-footer {
    background-color: #26272b;
    padding: 45px 0 20px;
    font-size: 15px;
    line-height: 24px;
    color: #737373;
    margin-top: auto;
}

.site-footer hr {
    border-top-color: #bbb;
    opacity: 0.5;
}

.site-footer hr.small {
    margin: 20px 0;
}

.site-footer h6,
.site-footer h5 {
    color: #fff;
    text-transform: uppercase;
    margin-top: 5px;
    letter-spacing: 2px;
}

.site-footer h6 {
    font-size: 16px;
    /* margin-bottom: 1.45rem; */
}

.site-footer h5 {
    font-size: 30px;
}

.site-footer a {
    color: #737373;
}

.site-footer a:hover {
    color: #3366cc;
    text-decoration: none;
}

.footer-links {
    padding-left: 0;
    list-style: none;
}

.footer-links li {
    display: block;
}

.footer-links a {
    color: #737373;
}

.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
    color: #3366cc;
    text-decoration: none;
}

.footer-links.inline li {
    display: inline-block;
}

.site-footer .social-icons {
    text-align: right;
}

.site-footer .social-icons a {
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin-left: 6px;
    margin-right: 0;
    border-radius: 100%;
    background-color: #33353d;
}

.copyright-text {
    margin: 0;
}

@media (max-width: 991px) {
    .site-footer [class^=col-] {
        margin-bottom: 30px;
    }
}

@media (max-width: 767px) {
    .site-footer {
        padding-bottom: 0;
    }

    .site-footer .copyright-text,
    .site-footer .social-icons {
        text-align: center;
    }
}

.social-icons {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
}

.social-icons li {
    display: inline-block;
    margin-bottom: 4px;
}

.social-icons li.title {
    margin-right: 15px;
    text-transform: uppercase;
    color: #96a2b2;
    font-weight: 700;
    font-size: 13px;
}

.social-icons a {
    background-color: #eceeef;
    color: #818a91;
    font-size: 16px;
    display: inline-block;
    line-height: 44px;
    width: 44px;
    height: 44px;
    text-align: center;
    margin-right: 8px;
    border-radius: 100%;
    transition: all .2s linear;
}

.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
    color: #fff;
    background-color: #29aafe;
}

.social-icons.size-sm a {
    line-height: 34px;
    height: 34px;
    width: 34px;
    font-size: 14px;
}

.social-icons a.linkedin:hover {
    background-color: #007bb6;
}

.social-icons a.github:hover {
    background-color: #222222;
}

@media (max-width: 767px) {
    .social-icons li.title {
        display: block;
        margin-right: 0;
        font-weight: 600;
    }
}

.slug {
    font-family: 'Source Code Pro', monospace;
    padding: 0px;
    margin: 0px;
    transition: all .2s ease-in-out;
}

.slug:hover {
    transform: scale(1.1);
}

[data-toggle="tooltip"] {
    cursor: pointer;
}

.link-unstyled,
.link-unstyled:visited,
.link-unstyled:hover,
.link-unstyled:active,
.link-unstyled:focus,
.link-unstyled:active:hover {
    text-decoration: none;
}

.pointer {
    color: black;
}

.pointer:hover {
    text-decoration: none;
}

#notfound {
    position: relative;
    height: 100vh;
}

#notfound .notfound {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.notfound {
    max-width: 530px;
    width: 100%;
    line-height: 1.4;
    text-align: center;
}

.notfound .notfound-404 {
    position: relative;
    height: 200px;
    margin: 0px auto 20px;
    z-index: -1;
}

.notfound .notfound-404 h1 {
    font-family: 'Montserrat', sans-serif;
    font-size: 236px;
    font-weight: 200;
    margin: 0px;
    color: #211b19;
    text-transform: uppercase;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.notfound .notfound-404 h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 28px;
    font-weight: 400;
    text-transform: uppercase;
    color: #211b19;
    background: #fff;
    padding: 10px 5px;
    margin: auto;
    display: inline-block;
    position: absolute;
    bottom: 0px;
    left: 0;
    right: 0;
}

@media only screen and (max-width: 767px) {
    .notfound .notfound-404 h1 {
        font-size: 148px;
    }
}

@media only screen and (max-width: 480px) {
    .notfound .notfound-404 {
        height: 148px;
        margin: 0px auto 10px;
    }
    .notfound .notfound-404 h1 {
        font-size: 86px;
    }
    .notfound .notfound-404 h2 {
        font-size: 16px;
    }
    .notfound a {
        padding: 7px 15px;
        font-size: 14px;
    }
}

#resolutions {
    font-family: 'Montserrat', sans-serif;
    margin-top: 5rem;
    margin: inherit auto;
}

#resolutions ul {
    list-style: none;
    line-height: 2rem;
    padding: 0;
}

.card {
    min-height: 50vh;
}

.card-subtitle span {
    transition: color .1s ease-in-out;
}

.card-subtitle span:not(:first-child) {
    margin-left: 1rem;
}

.card-subtitle span:hover {
    cursor: pointer;
}

.access-method-unlisted:hover {
    color: #73d673;
}
.access-method-private:hover {
    color: #ff6961;
}
.card-subtitle span:hover:not(#access-method) {
    color: black;
}

#notfound-generic {
    position: relative;
    height: 100vh;
}

#notfound-generic .notfound-generic {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.notfound-generic {
    max-width: 520px;
    width: 100%;
    line-height: 1.4;
    text-align: center;
}

.notfound-generic .notfound-generic-404 {
    position: relative;
    height: 240px;
}

.notfound-generic .notfound-generic-404 h1 {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 252px;
    font-weight: 900;
    margin: 0px;
    color: #262626;
    text-transform: uppercase;
    letter-spacing: -40px;
    margin-left: -20px;
}

.notfound-generic .notfound-generic-404 h1 > span {
    text-shadow: -8px 0px 0px #fff;
}

.notfound-generic .notfound-generic-404 h3 {
    font-family: 'Cabin', sans-serif;
    position: relative;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: #262626;
    margin: 0px;
    letter-spacing: 3px;
    padding-left: 6px;
}

.notfound-generic h2 {
    font-family: 'Cabin', sans-serif;
    font-size: 20px;
    font-weight: 400;
    text-transform: uppercase;
    color: #000;
    margin-top: 0px;
    margin-bottom: 25px;
}

@media only screen and (max-width: 767px) {
    .notfound-generic .notfound-generic-404 {
        height: 200px;
    }
    .notfound-generic .notfound-generic-404 h1 {
        font-size: 200px;
    }
}

@media only screen and (max-width: 480px) {
    .notfound-generic .notfound-generic-404 {
        height: 162px;
    }
    .notfound-generic .notfound-generic-404 h1 {
        font-size: 162px;
        height: 150px;
        line-height: 162px;
    }
    .notfound-generic h2 {
        font-size: 16px;
    }
}

body {
  background-color: #8395a7;
  background-attachment: fixed;
  background-size: cover;
}

